// 
const SIGNIN = "SIGNIN";
const SIGNOUT = "SIGNOUT";

//  
export const signin = (userInfo) => ({
   type: SIGNIN,
   payload: userInfo,
});
export const signout = () => ({ type: SIGNOUT });

const initialState = {
   signined: false,
   id: "",
};

export default function auth(state = initialState, action) {
   switch (action.type) {
      case SIGNIN:
         return {
            signined: action.payload.member,
            ...action.payload,
         };
      case SIGNOUT:
         return {
            signined: false,
            id: "",
         };
      default:
         return state;
   }
}
